.categoryForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px 20px;
    margin: 100px auto;
    border: 1px solid #ccc;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    background-color: white;
    width: 500px;
}

.title h3 {
    font-size: x-large;
    font-weight: 600;
    margin-bottom: 20px;
    color: gray;
    text-align: center;
}

.label {
    font-size: 0.9rem;
    color: #4a5568;
    margin-bottom: 6px;
    font-weight: 500;
    display: flex;
    align-items: center;
}

.inputGroup {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.inputWrapper {
    position: relative;
}

.tagIcon {
    position: absolute;
    left: 15px;
    top: 20px;
}

.textInput {
    border: 2px solid gray;
    border-radius: 8px;
    padding: 15px 15px 15px 45px;
    width: 80%;
    outline: none;
    background: #ffffff;
    color: #2d3748;
    font-size: 1rem;
    box-sizing: border-box;
    transition: all 0.3s ease-in-out;
}

.textInput::placeholder {
    color: #cbd5e0;
    font-weight: 400;
}

.textInput:focus {
    border-color: #4a90e2;
    box-shadow: 0 0 6px rgba(74, 144, 226, 0.4);
    background: rgba(255, 255, 255, 0.15);
}

.ButtonGroup {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 30px 0px 20px 0px;
}

.nextButton {
    padding: 15px 30px;
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 600;
    color: white;
    cursor: pointer;
    background: rgb(98, 201, 106);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
    letter-spacing: 0.5px;
}

.backButton{
     padding: 15px 30px;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  color: white;
  cursor: pointer;
  background: rgb(221, 86, 81);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  letter-spacing: 0.5px;
}