.productForm {
    background-color: white;
    border: 1px solid gray;
    border-radius: 12px;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    padding: 10px 10px 10px 30px;
    width: 600px;
    margin: auto;
}


.header h3 {
    text-align: center;
    font-size: 1.8rem;
    color: rgb(101, 101, 206);
    font-weight: 600;
    margin-bottom: 30px;
    letter-spacing: 1px;
}

.InputGroup {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 15px 0px;
    position: relative;
}

.InputGroup label {
    font-size: 0.9rem;
    color: #4a5568;
    margin-bottom: 6px;
    font-weight: 500;
    display: flex;
    align-items: center;
}

.inputWrapper {
    position: relative;
    width: 100%;
}

.inputWrapper input[type='text'],
.inputWrapper input[type='number'] {
    padding: 15px 15px 15px 45px;
    border: 2px solid #e2e8f0;
    width: 100%;
    border-radius: 14px;
    outline: none;
    background: #ffffff;
    color: #2d3748;
    font-size: 1rem;
    box-sizing: border-box;
    transition: all 0.3s ease;
}

.inputWrapper input[type='text']::placeholder,
.inputWrapper input[type='number']::placeholder {
    color: #cbd5e0;
    font-weight: 400;
}

.inputWrapper input[type='text']:focus,
.inputWrapper input[type='number']:focus {
    border-color: #4a90e2;
    box-shadow: 0 0 6px rgba(74, 144, 226, 0.4);
    background: rgba(255, 255, 255, 0.15);
}

.ButtonGroup {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 30px 0px 20px 0px;
}

.nextButton {
    padding: 15px 30px;
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 600;
    color: white;
    cursor: pointer;
    background: rgb(98, 201, 106);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
    letter-spacing: 0.5px;
}

.backButton {
    padding: 15px 30px;
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 600;
    color: white;
    cursor: pointer;
    background: rgb(221, 86, 81);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease;
    letter-spacing: 0.5px;
}

.nextButton:hover {
    background: rgb(75, 169, 201);
    transform: translateY(-3px);
}

.nextButton:active {
    transform: translateY(-1px);
}

.backButton:hover {
    background: rgb(75, 169, 201);
    transform: translateY(-3px);
}

.backButton:active {
    transform: translateY(-1px);
}





